.carousal-container {
    height: 60vh;
    width: 100%;
    overflow: hidden;
  }
  .video-calousal {
    filter: contrast(1.2) brightness(1.5);
    position: absolute;
    width: 100vw;
    height: 60vh;
    object-fit: cover;
    animation: videoc 3s linear 1.5s normal;
    z-index: -1;
  }
  @keyframes videoc {
    from {
      filter: blur(3px) contrast(1.2) brightness(1.5);
    }
    to {
      filter: blur(3px) contrast(1.2) brightness(1.5);
    }
  }
  .imgc11 {
    position: absolute;
    height: 23vh;
    width: 70vw;
    top: 120px;
    left: 0px;
    animation: slid 0.5s normal 0.5s 1 linear;
    outline: none;
    z-index: 2;
  }
  @keyframes slid {
    from {
      transform: translateX(-1000px);
    }
    to {
      transform: translateX(0px);
    }
  }
  .imgc12 {
    height: 15vh;
    width: 50vw;
    outline: none;
    z-index: 1;
    position: absolute;
    top: 0px;
    right: 0px;
    animation: slid2 1.5s normal 0s 1 linear;
  }
  @keyframes slid2 {
    from {
      transform: translateX(5000px);
    }
    to {
      transform: translateX(0px);
    }
  }
  .imgc13 {
    height: 40vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    bottom: 0px;
    animation: slid3 1.5s normal 0s 1 linear;
    z-index: 1;
  }
  @keyframes slid3 {
    from {
      transform: translatey(1000px);
    }
    to {
      transform: translatey(5vh);
    }
  }
  .carousal-slider {
    height: 80vh;
    width: 100%;
  }
  .slick-dots li.slick-active button:before {
    color: blue;
  }
  
  .slick-dots li button:before {
    font-size: 18px;
    color: rgb(253, 253, 253);
    z-index: 11;
  }
  .slick-dots {
    position: absolute;
    z-index: 11;
    bottom: 20px;
  }
  
  .element.style {
    outline: none !important;
  }
  .cimgwraper {
    position: relative;
    height: 60vh;
    outline: none !important;
    border: none !important;
  }
  